<template>
  <div style="display:flex;flex-direction:column;margin-left:5rem;width:12rem;">
    <p style="margin-left:2rem;color:deepskyblue;font-size:2rem;">修改昵称</p>
    <p style="margin-left:2rem;font-size:2rem;">{{ oldName }}</p>
    <input type="text" placeholder="请输入您的新昵称" v-model="targetName" style="border-radius:5px;margin-top:1rem;border:solid 1px black;height:30px;width:12rem;font-size:1rem;">
    <button @click="ChangeName" style="background-color:deepskyblue;color:white;border-radius:5px;margin-top:2rem;width:8rem;height:2rem;align-self:center;cursor:pointer;font-size:1.5rem;">确认修改</button>
  </div>
</template>

<script>
import axios from "axios";
import {getCurrentInstance, onMounted, ref} from "vue";
import router from "../Router/First";

export default {
setup() {
  const gci = getCurrentInstance()
  onMounted(async () => {
    document.title = "修改昵称"
    gci.data.id = gci.appContext.config.globalProperties.$id
    gci.data.mitt1 = gci.appContext.config.globalProperties.$mitt1
    axios.get('http://114.55.38.94:8001/GetNameById',{params:{"id":gci.data.id}}).then((res) => {
      gci.data.oldName = res.data.data
    })
  })
},
data(){
  return{
    oldName:"",
    targetName:"",
  }
},
methods:{
  ChangeName() {
    const that = this
    axios.get('http://114.55.38.94:8001/ChangeName', {params: {"id": this.id, 'name': this.targetName}}).then(function (res) {
      if(res.data.data=="修改昵称成功"){
        alert("修改昵称成功")
        that.mitt1.emit('Reload')
      }else{
        alert(res.data.data)
      }
    })
  },
}
}
</script>

